// базовые подключения
@import 'vars';
@import 'mixins';
@import 'fonts';
@import 'settings';

@import './components/header';
@import './components/hero';
@import './components/about';
@import './components/spectr';
@import './components/services';
@import './components/partners';
@import './components/reviews';
@import './components/faq';
@import './components/consult';
@import './components/footer';
@import './components/modal';

// cashback block

.cashback {
  margin-top: 50px;
  &__title {
    margin-bottom: 20px;
  }
  &__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    border-radius: 16px;
    padding: clamp(20px, 2vw, 40px);
    background: #2a2c2e url('../img/cashback-bg.webp') no-repeat;
    background-position: left 70px;
    background-size: contain;
    @media (max-width: 768px) {
      grid-template-columns: 1fr;
      background: #2a2c2e;
    }
  }

  &__desc {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: var(--white);
  }

  .form-consult__submit {
    margin-top: 20px;
  }
}

.i-futures {
  margin-bottom: 150px;
  @media (max-width: 992px) {
    margin-bottom: 75px;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    gap: 20px;
  }

  &__item {
    border-radius: 8px;
    padding: 14px 48px;
    background: var(--fon-belyy);
    @media (max-width: 992px) {
      padding: 14px 20px;
    }
    .number {
      font-family: var(--second-family);
      font-weight: 400;
      font-size: 64px;
      text-transform: uppercase;
      color: #206cdc;
      margin-bottom: 10px;
    }
    .text {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 16px;
      color: var(--white);
    }
  }
}

.i-cashback {
  margin-bottom: 150px;
  @media (max-width: 992px) {
    margin-bottom: 75px;
  }
  &__title {
    margin-bottom: 14px;

    span {
      display: block;
      color: var(--white);
      font-family: var(--second-family);
      font-weight: 400;
      font-size: 32px;
      text-transform: uppercase;
      text-align: right;
      color: #fff;
      -webkit-text-fill-color: #fff;
      @media (max-width: 768px) {
        display: none;
      }
    }
  }
  &__text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: var(--white);
  }
  &__wrapper {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    flex-wrap: wrap;
    border-radius: 16px;
    padding: 40px 80px;
    background: linear-gradient(
      159deg,
      #1561d1 0%,
      #658fcb 45%,
      #658fcb 71.91%,
      #1561d1 100%
    );
    @media (max-width: 768px) {
      padding: 40px 20px;
    }
  }
  &__btn {
    max-width: 100%;
    width: 100%;
  }

  &__right {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    @media (max-width: 768px) {
      display: flex;
      flex-direction: column-reverse;
      width: 100%;
    }
    .image {
      animation: scaling 10s linear infinite;
    }
  }
}

.i-advantages {
  margin-bottom: 150px;
  @media (max-width: 992px) {
    margin-bottom: 75px;
  }

  &__items {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }

  .item {
    flex: 1 1 45%;
    border-radius: 16px;
    padding: clamp(15px, 2vw, 30px);
    display: flex;
    flex-direction: column;
    gap: clamp(15px, 2vw, 30px);
    position: relative;
    overflow: hidden;
    background: #2a2c2e;
    @media (max-width: 992px) {
      flex: 1 1 auto;
    }
    .title {
      font-family: var(--second-family);
      font-weight: 400;
      font-size: clamp(32px, 2vw, 48px);
      text-transform: uppercase;
      color: var(--white);
    }

    .list {
      display: flex;
      flex-direction: column;
      gap: 14px;
      width: 25vw;
      max-width: 500px;
      @media (max-width: 992px) {
        max-width: 100%;
        width: 100%;
      }
    }

    li {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: clamp(14px, 1vw, 16px);
      line-height: 130%;
      color: #fff;
      position: relative;
      display: grid;
      grid-template-columns: 13px 1fr;
      align-items: flex-start;
      gap: 10px;

      &:before {
        content: '';
        width: 13px;
        height: 13px;
        border-radius: 50%;
        background: #1f2123;
        border: 1px solid var(--accent);
        transform: translateY(3px);
      }
    }

    .image {
      position: absolute;
      right: -80px;
      bottom: -100px;
      width: 20vw;
      max-width: 350px;
      @media (max-width: 992px) {
        display: none;
      }
    }
    &:nth-child(3) {
      .image {
        right: 0px;
        bottom: -50px;
        width: 100%;
        max-width: 650px;
      }
    }
  }
}

.block-iframe {
  margin-bottom: 150px;
  position: relative;
  &:after {
    content: '';
    position: absolute;
    width: 240px;
    height: 240px;
    bottom: -120px;
    right: 40px;
    background: url('../img/blur-circle-2.webp') no-repeat;
    background-size: cover;
    z-index: -1;
  }
  @media (max-width: 992px) {
    margin-bottom: 75px;
  }
  &__decor {
    position: absolute;
    top: -80px;
    left: 40%;
    @media (max-width: 992px) {
      display: none;
    }
  }
  &__wrapper {
    border-radius: 16px;
    padding: 80px 40px;
    background: #2a2c2e;
    position: relative;
    overflow: hidden;
    @media (max-width: 992px) {
      padding: 40px 20px;
    }
    @media (max-width: 768px) {
      display: flex;
      flex-direction: column;
      padding-bottom: 0;
    }
  }
  &__title {
    margin-bottom: 14px;
  }
  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: clamp(24px, 2vw, 32px);
    text-transform: uppercase;
    color: var(--white);
  }
  &__buttons {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    margin-top: clamp(30px, 3vw, 60px);
  }
  &__img {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 40vw;
    max-width: 750px;
    @media (max-width: 768px) {
      position: static;
      max-width: 100%;
      width: 100%;
      height: 100%;
      transform: translateX(20px);
    }
    img {
      display: block;
    }
  }
  @media (max-width: 768px) {
    .btn--accent {
      max-width: 100%;
      width: 100%;
    }
    .btn--border {
      max-width: 100%;
      width: 100%;
    }
  }
}

.block-iframe-osago {
  &:after {
    display: none;
  }
}
.s-info {
  margin-bottom: 150px;
  position: relative;
  .container {
    position: relative;
  }
  @media (max-width: 992px) {
    margin-bottom: 75px;
  }

  &__circle {
    position: absolute;
    top: 40px;
    right: 0;
    z-index: -1;
    width: 200px;
    height: 200px;
    animation: flare 30s linear infinite;
    @media (max-width: 768px) {
      display: none;
    }
  }

  &__tor {
    position: absolute;
    bottom: -60px;
    left: 60px;
    z-index: -1;
    width: 420px;
    height: 420px;
    animation: flare 40s linear infinite;
    @media (max-width: 768px) {
      display: none;
    }
  }

  &__title {
    text-wrap: balance;
    text-align: center;
    max-width: 1140px;
    margin: 0 auto;
    font-size: clamp(64px, 7vw, 96px);
    @media (max-width: 768px) {
      font-size: 38px;
    }
  }
}

.s-cashback {
  margin-bottom: 150px;
  @media (max-width: 992px) {
    margin-bottom: 75px;
  }

  &__container {
    border-radius: 16px;
    padding: 40px 80px;
    background: linear-gradient(
      159deg,
      #1561d1 0%,
      #658fcb 33.71%,
      #658fcb 78.05%,
      #1561d1 100%
    );
    @media (max-width: 992px) {
      padding: 20px 10px;
    }
  }

  &__head {
    display: flex;
    align-items: center;
    gap: 20px;
    flex-wrap: wrap;
    max-width: 1080px;
    margin: 0 auto;
    margin-bottom: 40px;
  }
  &__title {
    margin-bottom: 0;

    span {
      display: block;
      color: var(--white);
      font-family: var(--second-family);
      font-weight: 400;
      font-size: 32px;
      text-transform: uppercase;
      text-align: right;
      color: #fff;
      -webkit-text-fill-color: #fff;
    }
  }
  &__text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: var(--white);
    max-width: 460px;
  }

  &__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    @media (max-width: 768px) {
      grid-template-columns: 1fr;
    }
  }

  &__block {
    display: -ms-grid;
    display: grid;
    -ms-grid-columns: 1fr 1fr;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    .block__text {
      display: flex;
      flex-direction: column;
      align-items: end;
    }
    .image {
      -webkit-animation: scaling 10s linear infinite;
      animation: scaling 10s linear infinite;
    }
  }
  &__btn {
    max-width: 100%;
    width: 100%;
    margin-top: 14px;
  }
}

.s-advantages {
  margin-bottom: 150px;
  @media (max-width: 992px) {
    margin-bottom: 75px;
  }
  &__container {
    border-radius: 16px;
    background: #2a2c2e;
    padding: 80px 40px;
    position: relative;
    @media (max-width: 992px) {
      padding: 40px 20px;
    }
  }

  &__head {
    display: grid;
    grid-template-columns: 500px 1fr;
    position: relative;
    @media (max-width: 1100px) {
      grid-template-columns: 1fr;
    }
  }
  &__img {
    position: absolute;
    z-index: 1;
    right: 0;
    top: -90px;
    @media (max-width: 1440px) {
      max-width: 500px;
      top: 0;
    }
    @media (max-width: 1100px) {
      display: none;
    }
  }
  &__title {
    margin-bottom: 0;
    margin-bottom: 14px;
  }

  &__text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: #fff;
    max-width: 485px;
    margin-bottom: 54px;
  }
  &__btn {
    max-width: 100%;
    width: 100%;
  }

  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
    position: relative;
    z-index: 2;
    padding-top: 40px;
    @media (max-width: 1100px) {
      grid-template-columns: 1fr;
    }
  }

  &__col {
    display: flex;
    flex-direction: column;
    gap: 10px;
    height: 100%;
  }

  &__item {
    border-radius: 16px;
    padding: clamp(15px, 2vw, 30px);
    background: #1f2123;
    display: flex;
    flex-direction: column;
    height: 100%;
    .title {
      font-family: var(--second-family);
      font-weight: 400;
      font-size: 48px;
      text-transform: uppercase;
      color: var(--white);
      margin-bottom: 30px;
    }
    .list {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -ms-flex-direction: column;
      flex-direction: column;
      gap: 14px;
      width: 25vw;
      max-width: 500px;
      margin-bottom: 30px;
      flex: 1 1 auto;
      @media (max-width: 768px) {
        max-width: 100%;
        width: 100%;
      }
      li {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: clamp(14px, 1vw, 16px);
        line-height: 130%;
        color: #fff;
        position: relative;
        display: -ms-grid;
        display: grid;
        -ms-grid-columns: 13px 1fr;
        grid-template-columns: 13px 1fr;
        -webkit-box-align: start;
        -ms-flex-align: start;
        align-items: flex-start;
        gap: 10px;
        &:before {
          content: '';
          width: 13px;
          height: 13px;
          border-radius: 50%;
          background: #1f2123;
          border: 1px solid var(--accent);
          -webkit-transform: translateY(3px);
          -ms-transform: translateY(3px);
          transform: translateY(3px);
        }
      }
    }
    .bottom {
      display: flex;
      justify-content: space-between;
      gap: 20px;
      flex-wrap: wrap;
      align-items: center;
      @media (max-width: 768px) {
        align-items: flex-start;
      }
    }
    .price {
      font-family: var(--second-family);
      font-weight: 400;
      font-size: 48px;
      text-transform: uppercase;
      background: linear-gradient(
        159deg,
        #1561d1 0%,
        #658fcb 45%,
        #658fcb 71.91%,
        #1561d1 100%
      );
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
    .button {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 16px;
      text-transform: uppercase;
      color: #fff;
      border: 1px solid #206cdc;
    }
  }
  &__decor {
    position: absolute;
    bottom: -80px;
    left: 45%;
    transform: translateX(-50%);
  }
}
