.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,.5);
  backdrop-filter: blur(5px);
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  opacity: 0;
  visibility: hidden;
  transition: .2s ease;
  &.active {
    opacity: 1;
    visibility: visible;
  }
  &__container {
    background: #2a2c2e;
    max-width: 1000px;
    position: relative;
    width: 100%;
    border-radius: 16px;
    height: 100%;
    overflow: auto;
    display: flex;
    flex-direction: column;
  }

  &__close {
    position: absolute;
    top: 20px;
    right: 20px;
    transition: .2s ease;
    cursor: pointer;
    @media(max-width: 768px) {
      top: 10px;
    }
    svg {
      pointer-events: none;
    }
    @include hover {
      &:hover {
        transform: rotate(90deg);
      }
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: clamp(20px, 2vw, 40px);
    padding: 80px 40px 10px 40px;
    @media(max-width: 992px) {
      padding: 40px 20px;
    }
  }

  &__title {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 60px;
    text-transform: uppercase;
    text-align: center;
    color: var(--white);
    @media(max-width: 768px) {
      font-size: 32px;
      text-align: left;
    }
    span {
      background: linear-gradient(159deg, #1561d1 0%, #658fcb 45%, #658fcb 71.91%, #1561d1 100%);
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }
  &__img {
    margin-top: auto;
  }
}

.modal-form {
  &__block {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 10px;
    margin-bottom: 10px;
    input {
      border: 1px solid var(--divayder);
      border-radius: 8px;
      padding: 20px 48px;
      flex: 1 1 33%;

      font-family: var(--font-family);
      font-weight: 400;
      font-size: 16px;
      line-height: 130%;
      color: var(--white);
      background-color: transparent;

      &:focus {
        border-color: #fff;
        color: #fff;
      }
    }
    textarea {
      border: 1px solid var(--divayder);
      border-radius: 8px;
      padding: 20px 48px;
      flex: 1 1 33%;

      font-family: var(--font-family);
      font-weight: 400;
      font-size: 16px;
      line-height: 130%;
      color: var(--white);
      background-color: transparent;
      resize: none;
      &:focus {
        border-color: #fff;
        color: #fff;
      }
    }
  }
  &__btn {
    max-width: 100%;
    width: 100%;
    margin-top: 30px;
  }
}

.modal-frame {
  .modal__container {
    display: flex;
    justify-content: center;
  }

  
}