.reviews {
  margin-bottom: 150px;
  @media (max-width: 992px) {
    margin-bottom: 75px;
  }
  &__title {
    margin-bottom: 10px;
  }
  &__nav {
    display: flex;
    justify-content: end;
    gap: 10px;
    align-items: center;
    margin-bottom: 20px;
    .btn {
      cursor: pointer;
      svg {
        pointer-events: none;
        > * {
          transition: 0.2s ease;
        }
      }
      @include hover {
        &:hover {
          svg {
            rect {
              fill: var(--accent);
            }
          }
        }
      }
    }
  }

  &__slide {
    border-radius: 8px;
    padding: 10px 10px 40px 10px;
    display: flex;
    flex-direction: column;
    height: auto;
    background: #2a2c2e;
    box-sizing: border-box;
    .video {
      position: relative;
      margin-bottom: 30px;
      .play {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        cursor: pointer;
        z-index: 1;
        svg {
          pointer-events: none;
        }
      }
      video {
        max-width: 100%;
        width: 100%;
        height: auto;
        object-fit: cover;
        border-radius: 8px;
      }
    }

    .head {
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 20px;
      flex-wrap: wrap;
      margin-bottom: 20px;
    }
    .date {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 12px;
      color: #f6f6f6;
    }

    .rating {
      display: flex;
      align-items: center;
      gap: 10px;
      .text {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 12px;
        color: #f6f6f6;
      }
    }
    .desc {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 16px;
      line-height: 130%;
      color: var(--white);

      display: -webkit-box;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
}
