.header {
  background: linear-gradient(180deg, #1f2123 0%, rgba(31, 33, 35, 0) 100%);
  padding: 20px 0;
  position: relative;
  .mobile-actions {
    display: none;
    padding: 0 10px;
    margin-top: 40px;
    .header__cb {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
    @media (max-width: 1140px) {
      display: flex;
      flex-direction: column;
      gap: 20px;
    }
  }
  &__logo {
    @media (max-width: 1140px) {
      flex: 1 1 auto;
    }
  }
  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
  }

  &__actions {
    display: flex;
    gap: clamp(20px, 2vw, 40px);
    @media (max-width: 1140px) {
      display: none;
    }
  }

  &__cb {
    display: flex;
    flex-direction: column;
    gap: 5px;

    span {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 12px;
      text-align: center;
      color: #f6f6f6;
    }
  }

  &__phone {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 24px;
    color: var(--white);
    transition: color 0.2s ease;

    @include hover {
      &:hover {
        color: var(--accent);
      }
    }
  }

  .search {
    &__button {
      flex: 1 1 80px;
      border: 1px solid var(--white);
      padding: 16px 28px;
      border-radius: 8px;
      transition: 0.3s ease;
      @media (max-width: 1440px) {
        flex: 1 1 40px;
        padding: 8px 16px;
      }
      @include hover {
        &:hover {
          background-color: var(--accent);
          border-color: transparent;
        }
      }
    }
  }

  &__buttons {
    display: flex;
    align-items: center;
    gap: 10px;

    @media (max-width: 1440px) {
      .btn--border {
        display: none;
      }
    }
  }

  .submenu {
    position: absolute;
    top: 100px;
    left: 0;
    width: 100%;
    border-top: 1px solid var(--divayder);
    background: #2a2c2e;
    padding: 60px 10px;
    z-index: 9999;
    opacity: 0;
    visibility: hidden;
    transition: 0.2s ease;
    @media (max-width: 1140px) {
      position: static;
      opacity: 1;
      visibility: visible;
      margin-top: 20px;
      padding: 20px 10px;
      display: none;

      &.active {
        display: block;
      }
    }
    .submenu-wrapper {
      max-width: 950px;
      margin: 0 auto;
      display: flex;
      flex-direction: column;
      gap: 40px;
    }
    .submenu-group {
      display: grid;
      grid-template-columns: 180px 1fr;
      gap: 24px;
      @media (max-width: 1140px) {
        grid-template-columns: 1fr;
      }
    }
    .submenu-title {
      font-family: var(--second-family);
      font-weight: 400;
      font-size: clamp(18px, 2vw, 32px);
      text-transform: uppercase;
      background: linear-gradient(
        159deg,
        #1561d1 0%,
        #658fcb 45%,
        #658fcb 71.91%,
        #1561d1 100%
      );
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
    .submenu-list {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 14px;
      @media (max-width: 768px) {
        grid-template-columns: 1fr;
      }
      a {
        font-family: var(--second-family);
        font-weight: 400;
        font-size: 22px;
        text-transform: uppercase;
        color: #fff;
        transition: color 0.2s ease-in-out;
        @include hover {
          &:hover {
            color: var(--accent);
          }
        }
      }
    }
  }
}

.nav {
  .list {
    display: flex;
    gap: clamp(15px, 2vw, 30px);
  }

  .link {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 24px;
    text-transform: uppercase;
    color: var(--white);
    transition: color 0.2s ease;
    display: flex;
    align-items: center;
    gap: 5px;
    @include hover {
      &:hover {
        color: var(--accent);
        svg {
          path {
            stroke: var(--accent);
          }
        }
      }
    }
  }
  @media (max-width: 1140px) {
    position: absolute;
    top: 100px;
    left: -100%;
    width: 100%;
    height: 100vh;
    background-color: #1f2123;
    opacity: 0;
    visibility: hidden;
    transition: 0.2s ease-in;
    z-index: 3333;
    overflow: auto;
    padding-bottom: 300px;
    &.active {
      opacity: 1;
      visibility: visible;
      left: 0;
    }
    .list {
      flex-direction: column;
      padding-inline: 10px;
      padding-top: 20px;
    }
  }
}

.burger {
  @include burger;
}

@media (max-width: 1140px) {
  .sub-item .link {
    pointer-events: none;
  }
}

.sub-item a svg {
  pointer-events: none;
}

.sub-item a {
  position: relative;
  &:after {
    content: '';
    top: 0;
    left: 0;
    position: absolute;
    width: 100%;
    height: 120px;
    background: transparent;
    @media (max-width: 1140px) {
      display: none;
    }
  }
}

.sub-item:hover {
  .submenu {
    opacity: 1;
    visibility: visible;
  }
}
