.footer {
  padding-top: 150px;
  padding-bottom: 100px;
  @media (max-width: 768px) {
    padding-bottom: 40px;
  }
  &__container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 250px;
    gap: 20px;
    @media (max-width: 1140px) {
      grid-template-columns: 1fr 1fr;
    }
    @media (max-width: 768px) {
      grid-template-columns: 1fr;
    }
  }

  .col--nav {
    display: grid;
    grid-template-columns: 150px 150px;
    gap: 40px;
  }

  &__nav {
    .list {
      display: grid;
      gap: 20px;
    }

    .link {
      font-family: var(--second-family);
      font-weight: 400;
      font-size: clamp(20px, 2vw, 32px);
      text-transform: uppercase;
      color: var(--white);
      transition: color 0.2s ease;

      @include hover {
        &:hover {
          color: var(--accent);
        }
      }
    }
  }

  &__services {
    &-title {
      font-family: var(--second-family);
      font-weight: 400;
      font-size: clamp(20px, 2vw, 32px);
      text-transform: uppercase;
      color: var(--white);
      display: flex;
      align-items: center;
      gap: 10px;
      cursor: pointer;
      svg {
        pointer-events: none;
      }
      &.active {
        svg {
          transform: rotate(180deg);
        }
      }
    }
  }

  &__organization {
    .title {
      font-family: var(--second-family);
      font-weight: 400;
      font-size: clamp(20px, 2vw, 32px);
      text-transform: uppercase;
      color: var(--white);
      margin-bottom: 20px;
    }

    .list {
      display: flex;
      flex-direction: column;
      gap: 15px;

      li {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 16px;
        line-height: 130%;
        color: #f6f6f6;
      }
    }
  }

  &__reviews {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  &__review {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    border-radius: 8px;
    padding: 14px 20px;
    background: #859aba;
    transition: 0.2s ease;
    @include hover {
      &:hover {
        transform: translateX(-10px);
      }
    }
    .name {
      font-family: var(--second-family);
      font-weight: 400;
      font-size: clamp(18px, 2vw, 24px);
      text-transform: uppercase;
      color: #f6f6f6;
    }

    .rating {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: clamp(14px, 1vw, 16px);
      line-height: 130%;
      color: #f6f6f6;
      display: flex;
      align-items: center;
      gap: 3px;
    }
  }

  &__bottom {
    margin-top: 120px;
    padding-top: 40px;
    border-top: 1px solid var(--white);
    @media (max-width: 768px) {
      margin-top: 40px;
    }
    .container {
      display: flex;
      gap: clamp(30px, 3vw, 60px);
      flex-wrap: wrap;
      @media (max-width: 768px) {
        gap: 10px;
      }
    }
  }

  &__policy {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: var(--white);

    transition: color 0.2s ease;
    @include hover {
      &:hover {
        color: var(--accent);
      }
    }
  }
}

.footer__services-items {
  display: none;
  &.active {
    display: block;
  }
  .head {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 24px;
    text-transform: uppercase;
    color: var(--white);
    margin-bottom: 14px;
    margin-top: 10px;
  }
  .list {
    display: flex;
    flex-direction: column;
    gap: 5px;

    a {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 16px;
      line-height: 130%;
      color: var(--white);
      transition: color .2s ease;
      @include hover {
        &:hover {
          color: var(--accent);
        }
      }
    }
  }
}
