.about {
  padding-bottom: 150px;
  position: relative;
  overflow: hidden;
  &:after {
    content: '';
    position: absolute;
    left: 50%;
    bottom: 125px;
    transform: translateX(-50%);
    width: 350px;
    height: 350px;
    background: url('../../img/about-cube-blur.webp') center / contain no-repeat;
    z-index: -1;
  }
  &:before {
    content: '';
    position: absolute;
    left: 0;
    top: 30%;
    width: 250px;
    height: 250px;
    background-color: var(--white);
    fill: radial-gradient(
      50% 50% at 50% 50%,
      rgb(255, 255, 255) 0%,
      rgb(255, 255, 255) 100%
    );
    filter: blur(200px);
    opacity: 0.1;
  }
  @media (max-width: 992px) {
    padding-bottom: 75px;
  }
  &__image {
    animation: skew 10s linear infinite;
  }
  &__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: clamp(30px, 3.5vw, 60px);
    @media (max-width: 1140px) {
      grid-template-columns: 1fr;
    }
  }
  .right {
    display: flex;
    flex-direction: column;
    gap: 20px;

    .desc {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: clamp(14px, 1vw, 16px);
      line-height: 130%;
      color: #f6f6f6;
    }
    .subtitle {
      font-family: var(--second-family);
      font-weight: 400;
      font-size: clamp(32px, 3vw, 48px);
      text-transform: uppercase;
      color: #f6f6f6;
    }

    .items {
      display: flex;
      flex-wrap: wrap;
      gap: 20px;
    }
    .item {
      flex: 1 1 290px;
      border-radius: 8px;
      padding: 20px 30px;
      background: var(--fon-belyy);
      .number {
        font-family: var(--second-family);
        font-weight: 400;
        font-size: clamp(64px, 7vw, 96px);
        text-transform: uppercase;
        color: #206cdc;
      }
      .text {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 16px;
        color: #f6f6f6;
      }
    }
  }
}
