html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  overflow-x: hidden;
}

.page__body {
  margin: 0;
  min-height: 100%;
  font-size: 16px;
  overflow-x: hidden;
  background-color: var(--black);
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

@media (max-width: 1440px) {
  .container {
    max-width: 1280px;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.hidden-sm {
  @media (max-width: 768px) {
    display: none !important;
  }
}

.flex {
  display: flex;
}
.flex-reversed {
  flex-direction: row-reverse;
}

.title__section {
  font-family: var(--second-family);
  font-weight: 400;
  font-size: clamp(32px, 3vw, 64px);
  text-transform: uppercase;
  color: var(--white);
  margin: 0;
  margin-bottom: clamp(30px, 3vw, 60px);

  span {
    background: linear-gradient(
      159deg,
      #1561d1 0%,
      #658fcb 45%,
      #658fcb 71.91%,
      #1561d1 100%
    );
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}

.text--small {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 130%;
  color: #f6f6f6;
  margin: 0;
}

.btn--light {
  background: linear-gradient(
    159deg,
    #1561d1 0%,
    #658fcb 45%,
    #658fcb 71.91%,
    #1561d1 100%
  );
  border-radius: 8px;
  padding: 16px 28px;
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 16px;
  text-transform: uppercase;
  color: var(--white);
  transition: opacity 0.2s ease;
  @media (max-width: 1440px) {
    padding: 16px 16px;
  }
}

.btn--accent {
  border-radius: 8px;
  padding: 20px 48px;
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 16px;
  text-transform: uppercase;
  color: var(--white);
  background-color: var(--accent);
  transition: opacity 0.2s ease;
}

.btn--border {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 16px;
  text-transform: uppercase;
  color: #f6f6f6;
  border: 1px solid var(--divayder);
  border-radius: 8px;
  padding: 16px 28px;
  transition: 0.2s ease;
}

@media (any-hover: hover) {
  .btn--light:hover {
    opacity: 0.8;
  }
  .btn--accent:hover {
    opacity: 0.8;
  }

  .btn--border:hover {
    background-color: var(--accent);
    border-color: transparent;
  }
}

@keyframes cone {
  0% {
    transform: rotate(0deg) translateX(0);
  }
  30% {
    transform: rotate(20deg) translateX(-20px);
    animation-timing-function: ease-in-out;
  }
  50% {
    transform: rotate(30deg) translateX(-30px);
    animation-timing-function: ease-in;
  }
  75% {
    transform: rotate(15deg) translateX(-15px);
  }
  100% {
    transform: rotate(0deg) translateX(0);
  }
}

@keyframes scaling {
  0% {
    transform: scale(1);
  }
  25% {
    transform: scale(1.03);
  }
  50% {
    transform: scale(1.05);
  }
  75% {
    transform: scale(1.02);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes moving {
  0% {
    transform: translateX(0) rotate(0deg);
  }
  25% {
    transform: translateX(-25px) rotate(-8deg);
  }
  50% {
    transform: translateX(-50px) rotate(-12deg);
  }

  100% {
    transform: translateX(0) rotate(0deg);
  }
}

@keyframes movingRight {
  0% {
    transform: translateX(0) rotate(0deg);
  }
  25% {
    transform: translateX(25px) rotate(8deg);
  }
  50% {
    transform: translateX(50px) rotate(12deg);
  }

  100% {
    transform: translateX(0) rotate(0deg);
  }
}

@keyframes skew {
  0% {
    transform: skew(0, 0deg);
  }
  25% {
    transform: skew(0, 5deg);
  }
  50% {
    transform: skew(0, -5deg);
  }
  75% {
    transform: skew(0, 5deg);
  }
  100% {
    transform: skew(0, 0deg);
  }
}
