.spectr {
  position: relative;
  padding-bottom: 350px;
  padding-top: 300px;
  background: url('../../img/blur-spectr.png') no-repeat;
  background-size: contain;
  background-position: center 100px;
  @media (max-width: 768px) {
    padding-top: 40px;
    padding-bottom: 100px;
  }
  &:before {
    content: '';
    position: absolute;
    height: 100%;
    width: 76%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: url('../../img/circle.svg') center no-repeat;
    background-size: cover;
    z-index: 1;
    @media (max-width: 992px) {
      width: 100%;
    }
    @media (max-width: 768px) {
      display: none;
    }
  }
  &__cube {
    position: absolute;
    width: 220px;
    height: 220px;
    background: url('../../img/spectr-cube.png') no-repeat;
    animation: flare 30s linear infinite;
    @media (max-width: 768px) {
      width: 120px;
      height: 120px;
      background-size: contain;
    }
  }
  &__items {
    position: absolute;
    max-width: 1055px;
    width: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 806px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    z-index: 2;
    @media (max-width: 640px) {
      position: static;
      height: auto;
      transform: translate(0);
      gap: 20px;
    }
  }
  &__row {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &:first-child {
      justify-content: center;
    }
    &:nth-child(2) {
      max-width: 1000px;
      width: 100%;
      margin: 0 auto;
    }
    &:nth-child(3) {
      max-width: 790px;
      width: 100%;
      margin: 0 auto;
    }
    &:nth-child(5) {
      max-width: 980px;
      width: 100%;
      margin: 0 auto;
    }
    &:last-child {
      justify-content: center;
    }
    @media (max-width: 768px) {
      max-width: 100% !important;
      width: 100%;
      flex-direction: column;
      gap: 20px;
    }
  }
  &__item {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: clamp(16px, 2vw, 24px);
    text-transform: uppercase;
    color: #f6f6f6;
    border-radius: 100px;
    padding: 8px 48px;
    position: relative;
    background: linear-gradient(
      159deg,
      #1561d1 0%,
      #658fcb 45%,
      #658fcb 71.91%,
      #1561d1 100%
    );
    text-align: center;
    span {
      position: relative;
      z-index: 1;
    }
    & .hover-img {
      position: absolute;
      width: 100px;
      height: 100px;
      right: -45px;
      top: 0; // -20
      opacity: 0;
      visibility: hidden;
      transition: 0.3s ease-in-out;
    }
    @media (any-hover: hover) {
      &:hover {
        .hover-img {
          opacity: 1;
          visibility: visible;
          top: -20px;
        }
      }
    }
    @media (max-width: 768px) {
      width: 100%;
    }
  }
  &__title {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    gap: clamp(20px, 2vw, 40px);
    font-family: var(--second-family);
    font-weight: 400;
    font-size: clamp(64px, 5vw, 96px);
    text-transform: uppercase;
    text-align: center;
    color: #f6f6f6;

    &--gradient {
      font-family: var(--second-family);
      font-weight: 400;
      font-size: clamp(34px, 3vw, 48px);
      text-transform: uppercase;
      background: linear-gradient(
        159deg,
        #1561d1 0%,
        #658fcb 45%,
        #658fcb 71.91%,
        #1561d1 100%
      );
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }

    &--accent {
      font-family: var(--second-family);
      font-weight: 400;
      font-size: clamp(34px, 3vw, 48px);
      text-transform: uppercase;
      color: var(--accent);
    }
  }

  &__tor {
    position: absolute;
    right: -150px;
    bottom: 0;
    width: 350px;
    height: 350px;
    background: url('../../img/tor.webp') center / contain no-repeat;
    animation: flare 25s linear infinite;
    @media (max-width: 768px) {
      display: none;
    }
  }
}

@keyframes flare {
  0% {
    transform: translateY(0) rotate(0deg);
  }
  25% {
    transform: translateY(-50px) rotate(180deg);
  }
  50% {
    transform: translateY(50px) rotate(360deg);
  }
  75% {
    transform: translateY(75px) rotate(-180deg);
  }
  100% {
    transform: translateY(0) rotate(0deg);
  }
}
