.faq {
  margin-bottom: 150px;
  position: relative;
  &:after {
    content: '';
    position: absolute;
    top: 40px;
    right: 50%;
    width: 550px;
    height: 550px;
    background: url('../../img/blur-cube.png') no-repeat;
    top: -130px;
    right: 30%;
    z-index: -1;
  }
  @media (max-width: 1280px) {
    margin-bottom: 75px;
  }

  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 40px;
    row-gap: 20px;
    @media (max-width: 768px) {
      grid-template-columns: 1fr;
    }
  }

  &__item {
    display: grid;
    border-bottom: 1px solid var(--white);
    padding-bottom: 10px;
    cursor: pointer;
    > * {
      pointer-events: none;
    }
    &-header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 20px;
    }
    .arrow {
      transition: 0.2s ease-in-out;
    }
    &-title {
      font-family: var(--second-family);
      font-weight: 400;
      font-size: clamp(18px, 1vw, 24px);
      text-transform: uppercase;
      color: var(--white);
    }
    &-bottom {
      display: grid;
      grid-template-rows: 0fr;
      transition: 0.2s ease-in;
    }
    &-text {
      overflow: hidden;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 16px;
      line-height: 130%;
      color: var(--white);
    }
  }
}

.faq__item-active {
  .faq__item-bottom {
    grid-template-rows: 1fr;
    margin-top: 20px;
  }
  .arrow {
    transform: rotate(-180deg) translateX(-35px) translateY(-34px);
  }
}
