.services {
  margin-top: 40px;
  margin-bottom: 150px;
  @media (max-width: 992px) {
    margin-bottom: 75px;
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(390px, 1fr));
    gap: 20px;
    @media (max-width: 1440px) {
      grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    }
  }

  &__item {
    position: relative;
    border-radius: 8px;
    padding: 20px 48px;
    background: var(--fon-belyy);
    display: flex;
    flex-direction: column;
    position: relative;

    &:before {
      content: '';
      border-radius: 8px;
      position: absolute;
      inset: 0;
      background: var(--gradient-blue);
      opacity: 0;
      transition: 0.2s ease;
    }
    .link {
      position: absolute;
      inset: 0;
      z-index: 2;
    }
    .title {
      font-family: var(--second-family);
      font-weight: 400;
      font-size: 32px;
      text-transform: uppercase;
      color: #f6f6f6;
      padding-bottom: 40px;
      position: relative;
    }
    .image {
      position: absolute;
      bottom: 0;
      left: 0;
      img {
        display: block;
        filter: grayscale(1);
        transition: 0.5s ease;
      }
    }
    .arrow {
      display: flex;
      justify-content: end;
      position: relative;
    }

    @include hover {
      &:hover {
        &:before {
          opacity: 1;
        }
        .image {
          img {
            filter: grayscale(0);
          }
        }
      }
    }
  }
}
