:root {
  // base
  --font-family: "Inter", sans-serif;
  --second-family: "Bebas Neue Cyrillic", sans-serif;
  --content-width: 1640px;
  --container-offset: 10px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --divayder: rgba(255, 255, 255, 0.2);
	--fon-belyy: rgba(255, 255, 255, 0.1);
  --gradient-blue: linear-gradient(159deg, #1561d1 0%, #658fcb 45%, #658fcb 71.91%, #1561d1 100%);
  --white: #f6f6f6;
  --black: #1F2123;
  --full-white: #fff;
  --full-black: #000;
  --accent: #206cdc;
}
