.consult {
  &__title {
    margin-bottom: 20px;
  }
  &__container {
    background: #2a2c2e;
    border-radius: 16px;
    padding: 80px 40px;
    position: relative;
    overflow: hidden;
    @media (max-width: 992px) {
      padding: 40px 20px;
    }
  }
  &__head {
    margin-bottom: 38px;
  }

  &__desc {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: var(--white);
  }
  &__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 40px;
    @media (max-width: 1440px) {
      grid-template-columns: 1fr;
    }
  }

  &__lside {
    border-radius: 16px;
    padding: clamp(20px, 2vw, 40px);
    background: linear-gradient(
      155deg,
      #206cdc 0%,
      #88a5cf 45%,
      #88a5cf 71.91%,
      #206cdc 100%
    );
  }

  &__call {
    display: flex;
    flex-direction: column;
    gap: 30px;
    .block {
      display: flex;
      flex-wrap: wrap;
      gap: clamp(20px, 2vw, 40px);
    }
    .title {
      font-family: var(--second-family);
      font-weight: 400;
      font-size: clamp(32px, 2vw, 48px);
      text-transform: uppercase;
      color: #f6f6f6;
    }
    .call-phones {
      display: flex;
      flex-direction: column;
      gap: 14px;
    }
    .call-phone {
      display: flex;
      align-items: center;
      gap: 4px;
      flex-wrap: wrap;
      a {
        font-family: var(--second-family);
        font-weight: 400;
        font-size: clamp(24px, 2vw, 32px);
        text-transform: uppercase;
        color: #f6f6f6;
        transition: color 0.2s ease;
        @include hover {
          &:hover {
            color: var(--black);
          }
        }
      }
      span {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 16px;
        line-height: 130%;
        color: #f6f6f6;
      }
    }

    .socials {
      display: flex;
      gap: 10px;

      .social {
        width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 5px;
        transition: background-color 0.2s ease;
        &.wh {
          background: #48c95f;
        }
        &.tg {
          background: #27a6e5;
        }
        @include hover {
          &:hover {
            background-color: var(--black);
          }
        }
      }
    }
  }
}

.form-consult {
  display: flex;
  flex-direction: column;
  gap: 10px;
  height: 100%;
  &__block {
    display: flex;
    align-items: center;
    gap: 10px;
    flex-wrap: wrap;

    textarea {
      height: 100px;
    }
  }

  &__input {
    border: 1px solid var(--divayder);
    border-radius: 8px;
    padding: 20px 48px;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 16px;
    color: var(--white);
    flex: 1 1 305px;
    resize: none;
    background-color: transparent;
    outline: none;
    &:focus {
      border-color: var(--accent);
    }
  }

  &__submit {
    margin-top: 55px;
    @media (max-width: 1440px) {
      margin-top: 20px;
    }
  }
}

.consult-2 {
  margin-bottom: 150px;
  .consult__container {
    &:before {
      content: '';
      position: absolute;
      top: -20px;
      right: 160px;
      width: 304px;
      height: 304px;
      background: url('../../img/blur-cone.png') no-repeat;
      background-size: contain;
    }
  }

  @media (max-width: 992px) {
    margin-bottom: 75px;
  }
  .consult__wrapper {
    grid-template-columns: 665px 1fr;
    @media (max-width: 992px) {
      grid-template-columns: 1fr;
    }
  }
  .consult__lside {
    background: transparent;
    padding: 0;
    border-radius: 0;
    @media (max-width: 992px) {
      display: none;
    }
  }

  .consult__image {
    position: absolute;
    top: -100px;
    animation: skew 10s linear infinite;
    width: 52vw;
    max-width: 870px;
  }
  .form-consult__submit {
    margin-top: 30px;
  }
}
