.hero {
  padding-top: 100px;
  padding-bottom: 223px;
  position: relative;
  overflow: hidden;
  max-width: 1920px;
  margin: 0 auto;
  background: url('../../img/blur-bg.webp') no-repeat;
  background-size: contain;
  background-position: center center;
  @media (max-width: 992px) {
    padding-bottom: 70px;
    padding-top: 40px;
    background-size: contain;
  }
  &__wrapper {
    display: grid;
    grid-template-columns: 730px 1fr;
    @media (max-width: 992px) {
      grid-template-columns: 1fr;
    }
  }
  &__desc {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: #fff;
    max-width: 460px;
    margin-bottom: clamp(30px, 3vw, 60px);
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 32px;
    text-transform: uppercase;
    color: #fff;
    span {
      background: linear-gradient(
        159deg,
        #1561d1 0%,
        #658fcb 45%,
        #658fcb 71.91%,
        #1561d1 100%
      );
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }
  &__title {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: clamp(64px, 6vw, 96px);
    text-transform: uppercase;
    color: var(--white);
    margin: 0;
    margin-bottom: clamp(30px, 3vw, 60px);
    @media (max-width: 768px) {
      font-size: 48px;
    }
    &--accent {
      background: linear-gradient(
        159deg,
        #1561d1 0%,
        #658fcb 45%,
        #658fcb 71.91%,
        #1561d1 100%
      );
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
    &--row {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      gap: 20px;
      span {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 16px;
        line-height: 130%;
        color: var(--white);
        max-width: 190px;
      }
    }
  }

  &__images {
    position: absolute;
    width: 100%;
    height: 100%;
    @media (max-width: 992px) {
      position: static;
    }
  }
  &__image {
    position: absolute;
    @media (max-width: 992px) {
      position: relative;
    }
    &.shield {
      z-index: 1;
      top: -150px;
      animation: skew 12s linear infinite;
      filter: drop-shadow(0 0 10px rgba(255, 255, 255, 0.3));
      @media (max-width: 992px) {
        display: none;
      }
    }
    &.conus {
      z-index: 2;
      left: 40px;
      top: 130px;
      animation: skew 8s linear infinite;
      @media (max-width: 992px) {
        display: none;
      }
    }
    &.ruble-1 {
      bottom: 100px;
      z-index: 2;
      animation: moving 10s linear infinite;
      @media (max-width: 992px) {
        display: none;
      }
    }
    &.ruble-2 {
      right: 57%;
      bottom: 150px;
      animation: movingRight 8s linear infinite;
      @media (max-width: 992px) {
        display: none;
      }
    }
  }
  &__btn {
    @media (max-width: 768px) {
      max-width: 100%;
      width: 100%;
    }
  }
}

.hero-i {
  background: none;
  padding-bottom: 110px;
  .hero__images {
    max-width: 940px;
    @media (max-width: 1440px) {
      max-width: 720px;
    }
  }
  .hero__title {
    margin-bottom: 10px;
  }
}
