.partners {
  padding-bottom: 150px;
  position: relative;
  &:after {
    content: '';
    position: absolute;
    left: 250px;
    bottom: 0;
    width: 284px;
    height: 284px;
    background: url('../../img/cone.png') no-repeat;
    background-size: contain;
    animation: moving 5s linear infinite;
    @media (max-width: 1440px) {
      width: 200px;
      height: 200px;
    }
  }
  @media (max-width: 992px) {
    margin-bottom: 75px;
  }
  &__title {
    margin-bottom: 40px;
  }
  &__wrapper {
    display: grid;
    grid-template-columns: 600px 1fr;
    gap: clamp(30px, 2vw, 60px);
    @media (max-width: 1440px) {
      grid-template-columns: 1fr;
    }
  }

  .left {
  }
  .right {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }
  &__logo {
    flex: 1 1 226px;
    max-width: 226px;
    @media (max-width: 768px) {
      max-width: 100%;
      img {
        width: 100%;
      }
    }
  }
  .desc {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: var(--white);
    margin: 0;
    margin-bottom: 20px;
  }

  &__actions {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;

    .action {
      border-radius: 100px;
      text-align: center;
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 16px;
      line-height: 130%;
      color: #f6f6f6;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 8px 48px;
     
      &.btn--border {
        border-color: #206cdc;
        max-width: 145px;
        width: 100%;
      }
      @media(max-width: 992px) {
        width: 100%;
        max-width: 100% !important;
      }
    }
  }
}
